      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">:polyline.sync</span>=<span class="hljs-string">"polyline1"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-polyline</span>
          <span class="hljs-attr">:positions</span>=<span class="hljs-string">"positions1"</span>
          <span class="hljs-attr">:material</span>=<span class="hljs-string">"material1"</span>
          <span class="hljs-attr">:width</span>=<span class="hljs-string">"5"</span>
          <span class="hljs-attr">:clampToGround</span>=<span class="hljs-string">"false"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-polyline</span>&gt;</span>
