      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">:polyline.sync</span>=<span class="hljs-string">"polyline2"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-polyline</span> <span class="hljs-attr">:positions</span>=<span class="hljs-string">"positions2"</span> <span class="hljs-attr">:material</span>=<span class="hljs-string">"material2"</span> <span class="hljs-attr">:width</span>=<span class="hljs-string">"10"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-polyline</span>&gt;</span>
