      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">:polyline.sync</span>=<span class="hljs-string">"polyline3"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-polyline</span> <span class="hljs-attr">:positions</span>=<span class="hljs-string">"positions3"</span> <span class="hljs-attr">:material</span>=<span class="hljs-string">"material3"</span> <span class="hljs-attr">:width</span>=<span class="hljs-string">"10"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-polyline</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">polyline1</span>: {},
        <span class="hljs-attr">positions1</span>: [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">90</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">20</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">10000</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">120</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">20</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">10000</span> }
