          { <span class="hljs-attr">lng</span>: <span class="hljs-number">120</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">30</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">10000</span> }
        ],
        <span class="hljs-attr">material2</span>: <span class="hljs-literal">undefined</span>,
