        <span class="hljs-keyword">this</span>.material3 = <span class="hljs-keyword">new</span> Cesium.PolylineArrowMaterialProperty(Cesium.Color.PURPLE)
        <span class="hljs-keyword">this</span>.positions3 = [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">90</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">10000</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">120</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">10000</span> }
